.highlight {
  color: #fcaf58; }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  max-height: 100vh;
  margin: 0;
  color: #0d090a;
  background: #546a76;
  font-family: Arial, Helvetica, sans-serif; }

.main {
  width: 100%;
  height: 100vh;
  margin-top: 50px;
  display: grid;
  grid-template-areas: "header" "content" "footer";
  grid-template-rows: auto 1fr 50px; }

.navigation {
  background-color: #ebebeb;
  position: fixed;
  margin: 0;
  min-height: 50px;
  width: 100%;
  top: 0;
  box-shadow: 0 8px 4px -6px black;
  border-radius: 0 0 6px 6px;
  z-index: 99; }
  .navigation__list {
    list-style: none;
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start; }
    .navigation__list__item {
      box-sizing: border-box;
      color: #284b63;
      text-decoration: none;
      padding: 10px; }
      .navigation__list__item:hover {
        background-color: #d2d6d8;
        border-radius: 20px; }

.navigation__button {
  background-color: transparent;
  margin: 10px;
  border: none;
  cursor: pointer;
  display: none; }
  .navigation__button--line {
    fill: none;
    stroke: #546a76;
    stroke-width: 4;
    transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1); }
  .navigation__button--line1 {
    stroke-dasharray: 60 207;
    stroke-width: 6; }
  .navigation__button--line2 {
    stroke-dasharray: 60 60;
    stroke-width: 6; }
  .navigation__button--line3 {
    stroke-dasharray: 60 207;
    stroke-width: 6; }

.opened .navigation__button--line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6; }

.opened .navigation__button--line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6; }

.opened .navigation__button--line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6; }

.header,
.content {
  max-width: 1000px;
  width: 100%;
  margin: auto;
  padding: 0 20px; }

.header {
  grid-area: header;
  display: flex;
  justify-content: center; }
  .header__typewriter {
    min-height: 180px;
    min-width: 250px;
    max-width: 500px;
    width: 100%;
    background-color: #ebebeb;
    border-radius: 20px;
    box-sizing: border-box;
    font-size: 15px;
    font-family: "Courier New";
    white-space: pre-wrap;
    padding: 5px; }
    .header__typewriter:after {
      content: "|";
      animation: blink 500ms linear infinite alternate; }
    .header__typewriter__span--margin {
      margin-left: -100px; }
    .header__typewriter__span--highlight {
      color: #fcaf58; }

.content {
  grid-area: content; }

.footer {
  grid-area: footer;
  margin: 0;
  padding: 5px 5px 5px 15px;
  background-color: #ebebeb;
  box-shadow: 0 8px 4px -6px black;
  border-radius: 6px 6px 0 0; }

@media (max-width: 600px) {
  .navigation {
    min-height: 40px;
    width: 50px; }
  .navigation__list {
    display: none;
    padding-left: 5px; }
  .navigation--visiable {
    display: flex;
    flex-direction: column;
    width: 100%; }
  .navigation__list__item {
    padding: 1.5px 5px;
    margin: 10px; }
  .navigation__button {
    display: flex;
    z-index: 100; } }

@media (max-width: 500px) {
  body {
    padding: 0; }
  .header {
    padding: 5px; }
    .header__typewriter {
      font-size: 12px; } }

@-webkit-keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-moz-keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
